<template>
  <div>
    <label for="imageId">Enter Image ID:</label>
    <input v-model="imageId" id="imageId" type="text">
    <button @click="getImagePath">Get Image Path</button>
    <div v-if="imagePath">
      <img :src="imagePath" alt="Image">
    </div>
    <div v-else>
      <p>{{ message }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      imageId: "", // 用户输入的图像ID
      imagePath: null, // 存储图像路径
      message: "", // 用于显示消息
    };
  },
  methods: {
    getImagePath() {
      // 向后端发送GET请求获取图像路径
      // 使用axios或其他HTTP库发送请求
      axios.get(`/api/images/showimg/${this.imageId}`)
        .then(response => {
          this.imagePath = response.data;
          this.message = ""; // 清空消息
        })
        .catch(error => {
          console.error("Error fetching image path:", error);
          this.imagePath = null;
          this.message = "Image not found"; // 设置消息
        });
    },
  },
};
</script>
