<template>
  <div>
    <h1>文件上传</h1>
    <form enctype="multipart/form-data" @submit.prevent="uploadFile">
      <div>
        <label for="file">选择文件：</label>
        <input type="file" ref="fileInput" />
      </div>
      <div>
        <label for="name">名称：</label>
        <input type="text" v-model="formData.name" />
      </div>
      <div>
        <label for="class1">Class1：</label>
        <input type="text" v-model="formData.class1" />
      </div>
      <div>
        <label for="class2">Class2：</label>
        <input type="text" v-model="formData.class2" />
      </div>
      <div>
        <label for="weight">Weight：</label>
        <input type="text" v-model="formData.weight" />
      </div>
      <div>
        <button type="submit">上传文件</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formData: {
        name: "",
        class1: "",
        class2: "",
        weight: "",
      },
    };
  },
  methods: {
    uploadFile() {
      // 获取文件输入框的文件
      const file = this.$refs.fileInput.files[0];
      
      // 创建一个 FormData 对象，用于将文件和表单数据一起发送到服务器
      const formData = new FormData();
      formData.append("file", file);
      formData.append("name", this.formData.name);
      formData.append("class1", this.formData.class1);
      formData.append("class2", this.formData.class2);
      formData.append("weight", this.formData.weight);

      // 使用 Axios 或其他 HTTP 客户端库将数据发送到服务器的 /api/images/upload 端点
      // 这里需要根据您的实际项目设置服务器地址和端点
      axios.post("/api/images/upload", formData)
        .then((response) => {
          // 处理成功上传的响应
          console.log(response.data);
          alert("文件上传成功");
        })
        .catch((error) => {
          // 处理上传失败的情况
          console.error(error);
          alert("文件上传失败");
        });
    },
  },
};
</script>
